@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&display=swap);
/* base styles & title */
body{
  font-family: 'Comfortaa', cursive;
  color: #4e4e4e;
  background-color: #f7edec;
}
.App{
  max-width: 960px;
  margin: 5px auto;
}
.title h1{
  color: #ff4a4a;
  opacity: 0.92;
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

 /* Upload Portion */
 
form{
  margin: 30px auto 10px; 
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label span{
  margin-left: 7px;
}
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: #ff4a4a;
}

/* Progress Bar Styling */

.progress-bar{
  height: 5px;
  background: #e99d97 !important;
  margin-top: 20px;
}


/* Image Grid */

.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  object-fit: cover;
}

.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
   /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
  border-radius: 10px;
}

.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.45s;
  object-fit: cover;
}

.img-wrap img:hover{
  transform: scale(1.04);

}

.img-wrap .closes {
  position: absolute;
  top: 8px;
  right: 8px;
  z-index: 100;
  background-color: transparent;
  padding: 4px 3px;
  color: rgba(255, 255, 255, 0.651);
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  font-size: 30px;
  line-height: 10px;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.4s;
}
.img-wrap:hover .closes {
  opacity: 1;
}

.closes:hover{
  transform: scale(1.2);
}


/* Modal */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  transition: all 0.4s;
}

.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
}

