button {
  color: white;
  padding: 8px 25px;
  font-size: 1em;
  background-color: #ff4a4a;
  border-radius: 50px !important;
  border: 1px solid white;
  margin: 50px auto 0;
  cursor: pointer;
  opacity: 0.97;
  max-width: -webkit-fit-content !important ;
  max-width: -moz-fit-content !important ;
  max-width: fit-content !important ; 
}

.heading{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  color: #e99d97;
  margin-top: -50px;
}

